.socialmedia {
  svg {
    //Firefox bug fix
    position: relative;
    z-index: 1;
  }

  // transition
  transition: all 0.5s ease-out;
  background: inherit;
  background-position: 1% 50%;
  background-size: 300% 300%;
  align-items: center;
  @each $name, $pair in $button-colors {
    $color: nth($pair, 1);
    $color-invert: nth($pair, 2);

    &.is-#{$name} {
      background-color: $color;
      border-color: transparent;
      color: $color-invert;
      //social media unmade

      &.btn-ghost {
        border: 2px solid;
        border-color: transparent;

        color: $color-invert;
        background: $color;
        //background-image: radial-gradient(red, green, blue);
        background: 0 0;
        transition: 0.3s;
        box-shadow: inset 0 0 0 36px $color;
      }

      &.btn-ghost:hover {
        color: $color;
        background: transparent;
        border-radius: 50%;
        box-shadow: inset 0 0 0 0px $color;
      }
    }
  }
}
