$font-color:#0a0a0a;
.navbar{
  .navbar-item{
a{
    &:before{
        position:absolute;
        content:'';
        width:10%;
        height:10%;
        left:0;
        top:0;
        border-top:5px solid $font-color;
        border-left:5px solid $font-color;
        opacity:0;
        transition:0.5s;
    }
    &:after{
        position:absolute;
        content:'';
        width:10%;
        height:10%;
        right:0;
        bottom:0;
        border-bottom:5px solid $font-color;
        border-right:5px solid $font-color;
        opacity:0;
        transition:0.5s;
    }
    &:hover{
        color:black;
        background-color:white;

        &:before{
            width:100%;
        height:100%;
            opacity:1;
            left:0px;
            top:0px;
        }
        &:after{
            width:100%;
        height:100%;
            opacity:1;
            right:0px;
            bottom:0px;
        }
      }
      &.active{
        color:black;
        font-weight:500;
      }
}

  }}